import React from 'react';
import { Modal ,Select,List} from 'antd';
import echarts from 'echarts/lib/echarts';
// 引入柱状图
import  'echarts/lib/chart/bar';
import  'echarts/lib/chart/pie'
// 引入提示框和标题组件
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/title';
const Option = Select.Option;
export default class ProfitChart extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            value:props.value,
            styleWidth:props.styleWidth,
            id:props.id
        }
    }
    setData=()=>{
        console.log(this.state.styleWidth,this.state.id);
        var myChart = echarts.init(document.getElementById(this.state.id));
        var option = {
            title: {
                text:['{a|'+this.state.value.companyName+"("+this.state.value.companyCode+")"+"利润表 "+'}','{b|'+(this.props.sliderValue==2018?'2018年三季度报':'2018年年报')+'}'].join(''), 
                subtext: '单位：亿元',
                left:'20px',
                textStyle: {    
                    color: "#000000",
                    fontSize: 35,   
                    rich:
                    {
                        a:
                        {
                            color: "#000000",
                            fontSize: 25,
                        },
                        b:
                        {
                            color: '#777c83',
                            fontWeight: 700,
                            fontSize: 15,  
                        }
                    }
                },
                subtextStyle: {    
                    color: "#000000",
                    fontSize: 13, 
                    // lineHeight:200 
                },
                
            },
            tooltip: {
                 trigger: "item",  
            },
            // legend: {
            //     itemWidth:15,  
            //     itemHeight:15,  
            //     data:['可用','不可用'],
            // },

            
            xAxis: {
                type : 'category',
                //  data: ["营业收入","营业总成本","营业外收支","利润总额","其它"],
                splitLine:{
                    show:false,
                },
            },
            yAxis: {
                 splitLine:{
                    show:true,
                },
            },
            series: [{
                name: '营业收入',
                type: 'bar',
                data: [this.state.value.income],
                itemStyle:{
                     normal: {
                        borderColor:'#f4f8f9',
                        borderWidth: 2,
                        barBorderRadius:[5, 5, 5, 5]
                    }
        
                }
                
            },{
                name: '营业成本',
                type: 'bar',
                stack:'营业总成本',
                data: [this.state.value.revenue.revenue1],
                // itemStyle: {							
                //     normal: {								
                //         label: {									
                //             show: true, //开启显示
                //             position: 'inside', //在上方显示
                //             textStyle: { //数值样式
                //                 color: 'black',
                //                 fontSize: 16									
                //             }								
                //         }							
                //     }						
                // },
                itemStyle:{
                    //  normal:{color:"#FF8849"},
                     normal: {
                        borderColor:'#f4f8f9',
                        borderWidth: 2,
                        barBorderRadius:[5, 5, 5, 5]
                    }
        
                }

            },{
                name: '销售费用',
                type: 'bar',
                stack:'营业总成本',
                data: [this.state.value.revenue.revenue2],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            },{
                name: '管理费用',
                type: 'bar',
                stack:'营业总成本',
                data: [this.state.value.revenue.revenue3],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            },{
                name: '营业税金及附加',
                type: 'bar',
                stack:'营业总成本',
                data: [this.state.value.revenue.revenue4],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            },{
                name: '其他营业支出',
                type: 'bar',
                stack:'营业总成本',
                data: [this.state.value.revenue.revenue5],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            },{
                name: '营业外收支',
                type: 'bar',
                data: [this.state.value.incomeAndExpenditure],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            }
        
            ,{
                name: '所得税费用',
                type: 'bar',
                stack:'利润总额',
                data: [this.state.value.profit.profit1],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
                }
            },{
                name: '净利润',
                type: 'bar',
                stack:'利润总额',
                data: [this.state.value.profit.profit2],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
       
               }
            }
            
            ,{
                name: '少数股东权益',
                type: 'bar',
                stack:'其它',
                data: [this.state.value.other1],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
                }
            },{
                name: '归属于母公司所有者净利润',
                type: 'bar',
                stack:'其它',
                data: [this.state.value.other2],
                itemStyle:{
                    normal: {
                       borderColor:'#f4f8f9',
                       borderWidth: 2,
                       barBorderRadius:[5, 5, 5, 5]
                   }
                }
            }]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.clear();
        myChart.resize();
        myChart.setOption(option,true);
    }
    componentDidMount() {
        
    }
    componentWillReceiveProps(nextProps){
        this.setState({
            value:nextProps.value,
            styleWidth:nextProps.styleWidth,
            id:nextProps.id
          },this.setData);
      }
    render() {
        console.log(this.state.styleWidth);
        return (
            <div style={{display:'inline-block',verticalAlign:'top'}}>
                <div id={""+this.state.id} style={{ width: this.state.styleWidth, height: this.state.styleWidth }}></div>
            </div>
        );
    }
}