import React from 'react';
import { Modal ,Select,List,Input,Slider} from 'antd';
import "antd/lib/input/style/css"
import "antd/lib/slider/style/css"
import ProfitChart from './profitChart'
import SearchInput from './searchInput'
import EditableTagGroup from './editableTagGroup';
const Option = Select.Option;
const glValue1={
    companyName:'格力电器',
    companyCode:'000651',
    income:'1486.99',
    revenue:{
        revenue1:'1038.61',
        revenue2:'128.96',
        revenue3:'27.77',
        revenue4:'12.73',
        revenue5:'45.3'
    },
    incomeAndExpenditure:'-4.5',
    profit:{
        profit1:'39.22',
        profit2:'212.41',
    },
    other1:'100.23',
    other2:'211.18'
}
const glValue2={
    companyName:'格力电器',
    companyCode:'000651',
    income:'1981.23',
    revenue:{
        revenue1:'1382.34',
        revenue2:'189',
        revenue3:'43.66',
        revenue4:'17.42',
        revenue5:'63.48'
    },
    incomeAndExpenditure:'-8.39',
    profit:{
        profit1:'48.94',
        profit2:'263.79',
    },
    other1:'1.76',
    other2:'262.03'
}
const mdValue1={
    companyName:'美的集团',
    companyCode:'000333',
    income:'2057.57',
    revenue:{
        revenue1:'1496.65',
        revenue2:'243.97',
        revenue3:'56.3',
        revenue4:'12.92',
        revenue5:'50.01'
    },
    incomeAndExpenditure:'-14.31',
    profit:{
        profit1:'39.94',
        profit2:'223.6',
    },
    other1:'10.44',
    other2:'213.16'
}
const mdValue2={
    companyName:'美的集团',
    companyCode:'000333',
    income:'2596.65',
    revenue:{
        revenue1:'1881.65',
        revenue2:'310.86',
        revenue3:'95.72',
        revenue4:'16.18',
        revenue5:'71.95'
    },
    incomeAndExpenditure:'-15.88',
    profit:{
        profit1:'41.23',
        profit2:'216.5',
    },
    other1:'14.2',
    other2:'202.31'
}

export default class ProfitChartPage extends React.Component {
    constructor(props){
        super(props);
        this.state={
            valueList:[{
                companyName:'格力电器',
                companyCode:'000651',
                income:'1486.99',
                revenue:{
                    revenue1:'1038.61',
                    revenue2:'128.96',
                    revenue3:'27.77',
                    revenue4:'12.73',
                    revenue5:'45.3'
                },
                incomeAndExpenditure:'-4.5',
                profit:{
                    profit1:'39.22',
                    profit2:'212.41',
                },
                other1:'1.23',
                other2:'211.18'
            }],
            tags:['格力电器(000651)'],
            sliderValue:2018
        }
        
    }
    componentDidMount() {
        this.refs.ProfitChart0.setData();
    }
    handleClose = removedTag => {
        const tags = this.state.tags.filter(tag => tag !== removedTag);
        this.state.valueList.pop();
        const valueList=this.state.valueList;
        this.setState({ tags,valueList });
      };
    handleChange=(value)=>{
        console.log(value);
        if(this.state.valueList.length==2){
            return;
        }
        if(value=='000651'){
            if(this.state.valueList.length==1&&this.state.valueList[0].companyCode=='000651'){
                return;
            }
            this.state.valueList.push({
                companyName:'格力电器',
                companyCode:'000651',
                income:'1486.99',
                revenue:{
                    revenue1:'1038.61',
                    revenue2:'128.96',
                    revenue3:'27.77',
                    revenue4:'12.73',
                    revenue5:'45.3'
                },
                incomeAndExpenditure:'-4.5',
                profit:{
                    profit1:'39.22',
                    profit2:'212.41',
                },
                other1:'1.23',
                other2:'211.18'
            });
            this.setState({valueList:this.state.valueList},()=>{this.refs.ProfitChart0.setData();this.refs.ProfitChart1.setData();})
        }else if(value=='000333'){
            if(this.state.valueList.length==1&&this.state.valueList[0].companyCode=='000333'){
                return;
            }
            if(this.state.sliderValue==2018){
                this.state.valueList.push(mdValue1);
            }else if(this.state.sliderValue==2018.5){
                this.state.valueList.push(mdValue2);
            }
            this.state.tags.push('美的集团(000333)');
            this.setState({valueList:this.state.valueList},()=>{this.refs.ProfitChart0.setData();this.refs.ProfitChart1.setData();})
        }
        
    }
    sliderOnChange=(value)=>{
        console.log(value);
        const valueList=[];
        if(value==2018){
            valueList.push(glValue1);
        }else{
            valueList.push(glValue2);
        }
        if(this.state.valueList.length==2){
            if(value==2018){
                valueList.push(mdValue1);
            }else{
                valueList.push(mdValue2);
            }
        }
        this.setState({valueList,sliderValue:value});
    }
    render() {
        return (
            <div style={{background:'#f4f8f9',width:1450,height:800,margin:'0 auto',padding:50}}>
                <div style={{display:'inline-block',width:1100,height:800,verticalAlign:'top'}}>
                    <div style={{width: 1000,margin:'0 auto'}}>
                        {this.state.valueList.map((item,index)=>(
                            <ProfitChart ref={"ProfitChart"+index} value={item} id={item.companyCode} 
                                styleWidth={this.state.valueList.length>1?500:800}
                                sliderValue={this.state.sliderValue}/>
                        ))}
                    </div>
                </div> 
                <div style={{display:'inline-block',width:250,verticalAlign:'top',color:'#000000',fontSize:15,fontWeight:800}} >
                    请选择公司 (最多2家公司对比)
                    <SearchInput placeholder="输入公司名称/股票代码/首拼" style={{ width: 250 ,margin:'10px 0'}} 
                                 handleChange={(value)=>{this.handleChange(value)}}/>
                    已添加公司
                    <EditableTagGroup tags={this.state.tags} handleClose={this.handleClose}/>
                    日期
                    <Slider min={2018} max={2018.5} step={0.5} tooltipVisible
                        tipFormatter={(value)=>('2018Q'+((value-2018)/0.5+3))}
                        onChange={this.sliderOnChange}/>

                </div>
            </div>
        );
    }
}