import React from 'react';
import { Select } from 'antd';
import "antd/lib/select/style/css"
// import jsonp from 'fetch-jsonp';
import querystring from 'querystring';

const { Option } = Select;

let timeout;
let currentValue;

function judgegl(str){
    if('格力电器'.toLowerCase().indexOf(str)>=0
        ||'gldq'.toLowerCase().indexOf(str)>=0
        ||'000651'.toLowerCase().indexOf(str)>=0){
            return true;
        }
    return false;
}
function judgemd(str){
    if('美的集团'.toLowerCase().indexOf(str)>=0
        ||'mdjt'.toLowerCase().indexOf(str)>=0
        ||'000333'.toLowerCase().indexOf(str)>=0){
            return true;
        }
    return false;
}
function fetch(value, callback) {
//   if (timeout) {
//     clearTimeout(timeout);
//     timeout = null;
//   }
//   currentValue = value;

  function fake() {
    console.log(value);
    const data = [];
    if(judgegl(value)){
        data.push({value:'000651',text:'格力电器(000651)'})
    }
    if(judgemd(value)){
        data.push({value:'000333',text:'美的集团(000333)'})
    }
    callback(data);
  }
  fake();
//   timeout = setTimeout(fake, 300);
}

export default class SearchInput extends React.Component {
  state = {
    data: [],
    value: undefined,
  };

  handleSearch = value => {
    if (value) {
      fetch(value, data => this.setState({ data }));
    } else {
      this.setState({ data: [] });
    }
  };

//   handleChange = value => {
//     this.setState({ value });
//   };

  render() {
    const options = this.state.data.map(d => <Option key={d.value}>{d.text}</Option>);
    return (
      <Select
        showSearch
        value={this.state.value}
        placeholder={this.props.placeholder}
        style={this.props.style}
        defaultActiveFirstOption={false}
        showArrow={false}
        filterOption={false}
        onSearch={this.handleSearch}
        onChange={this.props.handleChange}
        notFoundContent={null}
      >
        {options}
      </Select>
    );
  }
}

